import React, { Component } from "react";
import { COPY_TO_CLIPBOARD } from "../../../../../../Services/Utils/utils";

class GetRightsCopyUrl extends Component {
    constructor() {
        super();
        this.state = { codeCopied: false };
    }
    codeCopy = (id) => {
        this.setState({
            codeCopied: true
        })
        setTimeout(() => {
            this.setState({
                codeCopied: false
            })
        }, 1500);
        COPY_TO_CLIPBOARD(id);
    }
    render() {
        const { codeCopied } = this.state
        const { requestedRespone, comment, modalAssetData } = this.props

        return requestedRespone && Object.keys(requestedRespone).length > 0 ? <div className="p-lg-6 p-3">
            <h5 className="mb-2 pt-1 text-center">Your UGC Request Sharing URL</h5>
            <p className="text-muted text-center">Copy and paste link in the comments or original post</p>
            <div className="mb-6">
                <textarea type="text" className="form-control form-control-lg textarea-min-row-3 bg-light" rows={4} placeholder="Add Comment" defaultValue={comment} readOnly />
            </div>
            <div className="mb-6">
                <div className="input-group mb-4">
                    <div className="input-group-text">
                        <span className="svg-icon svg-icon-2">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                <path fill="none" d="M0 0h24v24H0z" />
                                <path d="M13.06 8.11l1.415 1.415a7 7 0 0 1 0 9.9l-.354.353a7 7 0 0 1-9.9-9.9l1.415 1.415a5 5 0 1 0 7.071 7.071l.354-.354a5 5 0 0 0 0-7.07l-1.415-1.415 1.415-1.414zm6.718 6.011l-1.414-1.414a5 5 0 1 0-7.071-7.071l-.354.354a5 5 0 0 0 0 7.07l1.415 1.415-1.415 1.414-1.414-1.414a7 7 0 0 1 0-9.9l.354-.353a7 7 0 0 1 9.9 9.9z" fill="currentColor" />
                            </svg>
                        </span>
                    </div>
                    <input type="text" id="ugc_rights_url_" className="form-control form-control-lg" defaultValue={requestedRespone.bitlylink} readOnly />
                </div>
                <button type="button" className="btn btn-primary btn-lg w-100" onClick={() => { this.codeCopy('ugc_rights_url_') }}>
                    {codeCopied ? 'Copied' : 'Copy URL'}
                </button>
            </div>
            <div className="mb-6">
                <h6>Steps to proceed</h6>
                <ul className="list-unstyled list-points fs-7">
                    <li className="mb-3">You have successfully generated a "UGC Request" for this post.</li>
                    <li className="mb-3">Quickly review your Request before sending it to the owner.</li>
                    <li className="mb-3">Click on the "Generate Request" button located in the right bottom corner of the screen and then you will be redirected to the user's original post.</li>
                    <li className="mb-3">Paste the link of "UGC Request" in the comments section of the original post.</li>
                    <li className="mb-3">You will get UGC Rights asap post owner accept your request and allow you to use their post.</li>
                </ul>
            </div>
        </div> : null
    }
}

export default GetRightsCopyUrl;